@import './Variables.scss';

@mixin onTablet {
  @media (max-width: $tablet-max-width) {
    @content;
  }
}

@mixin onMobile {
  @media (max-width: $tablet-min-width) {
    @content;
  }
}

@mixin font_h1 {
  font-family: Mont-Bold, Arial, sans-serif;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: -0.01em;
}

@mixin font_h2 {
  font-family: Mont-Bold, Arial, sans-serif;
  font-size: 22px;
  line-height: 31px;
}

@mixin font_h3 {
  font-family: Mont-Semibold, Arial, sans-serif;
  font-size: 20px;
  line-height: 26px;
}

@mixin font_uppercase {
  font-family: Mont-Semibold, Arial, sans-serif;;
  font-size: 12px;
  line-height: 11px;
  text-transform: uppercase;
}

@mixin font_buttons {
  font-family: Mont-Semibold, Arial, sans-serif;;
  font-size: 14px;
  line-height: 21px;
}

@mixin font_body {
  font-family: Mont-Regular, Arial, sans-serif;;
  font-size: 14px;
  line-height: 21px;
}

@mixin font_small {
  font-family: Mont-Semibold, Arial, sans-serif;;
  font-size: 12px;
  line-height: 15px;
}
