@import '../../App.scss';

.productSlider {
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__controls {
    display: flex;
    gap: 16px;
  }

  &__button {
    width: 32px;
    height: 32px;
    border: 1px solid $icons-color;
    background-color: $white-color;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}
